@layer pixu {
  [pixu] {
    img {
      inline-size: 20rem;
      border-radius: 50%;
    }

    comparison {
      place-items: center;
    }

    right {
      display: flex;
      flex-flow: column nowrap;
      place-content: start;
      place-items: start;
      gap: 1.5rem;

      h6 {
        display: flex;
        place-content: start;
        place-items: center;

        gap: 1rem;
        font-size: 0.6em;

        text-align: left;

        img {
          inline-size: 4rem;
          border-radius: 0;

          :has(meta[name='color-scheme'][content='light']) & {
            filter: invert(1);
          }

          @media (prefers-color-scheme: light) {
            filter: invert(1);
          }
        }
      }
    }
  }
}
